.Screen-header {
    padding: 20px;
    padding-top: 10px;
    position: relative;
    padding-bottom: 150px;
    top: 0;
  }
  
  .Screen-title {
    font-size: 2.5em;
  }
  
  @media screen and (max-width: 680px) {
    .Screen-header {
      padding-top: 100px;
    }
  
    .Screen-title {
      font-size: 1.5em;
    }
  }