
input {
    margin: 1rem;
    width: 20rem;
    padding: 1rem .5rem;
  }
  .downshift-dropdown {
    margin: 0 auto;
    width: 20rem;
    border: 1px solid whitesmoke;
    border-bottom: none;
  }
  .dropdown-item {
    padding: 0.5rem;
    cursor: pointer;
    border-bottom: 1px solid whitesmoke;
    font-size: 1rem;
    text-align: left;
  }
  .dropdown-button {
    padding: 0.6rem;
    border-radius: 3px;
    background: white;
    cursor: pointer;
  }
  .popper-div {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 5rem;
  }
  .popper-item {
    padding: .5rem;
    border-bottom: 1px solid whitesmoke;
  }